export interface Anomaly {
  index: number;
  value: number;
  expected: number;
  deviation: number;
  severity: "low" | "medium" | "high";
  reason: string;
}

interface DataPoint {
  [key: string]: any;
}

/**
 * Detect anomalies in time-series data using statistical methods
 * Uses Z-score and moving average for detection
 */
export function detectAnomalies(
  data: DataPoint[],
  valueKey: string,
  options: {
    sensitivity?: number; // Standard deviations for threshold (default: 2)
    windowSize?: number; // Moving average window (default: 3)
  } = {}
): Anomaly[] {
  const { sensitivity = 2, windowSize = 3 } = options;

  if (!data || data.length < windowSize + 1) {
    return [];
  }

  const anomalies: Anomaly[] = [];
  const values = data.map((d) => Number(d[valueKey] || 0));

  // Calculate statistics
  const mean = values.reduce((sum, v) => sum + v, 0) / values.length;
  const variance =
    values.reduce((sum, v) => sum + Math.pow(v - mean, 2), 0) / values.length;
  const stdDev = Math.sqrt(variance);

  // Skip if standard deviation is too low (flat data)
  if (stdDev < mean * 0.01) {
    return [];
  }

  // Calculate moving average
  const movingAvg: number[] = [];
  for (let i = 0; i < values.length; i++) {
    if (i < windowSize - 1) {
      movingAvg.push(mean); // Use overall mean for early values
    } else {
      const window = values.slice(i - windowSize + 1, i + 1);
      const avg = window.reduce((sum, v) => sum + v, 0) / window.length;
      movingAvg.push(avg);
    }
  }

  // Detect anomalies using Z-score and moving average deviation
  values.forEach((value, index) => {
    if (index < windowSize) return; // Skip early values

    const expected = movingAvg[index];
    const deviation = Math.abs(value - expected);
    const zScore = (value - mean) / stdDev;
    const percentDeviation = (deviation / expected) * 100;

    // Anomaly criteria: Z-score threshold OR significant % deviation
    const isAnomaly =
      Math.abs(zScore) > sensitivity || percentDeviation > 20;

    if (isAnomaly) {
      // Determine severity
      let severity: "low" | "medium" | "high" = "low";
      if (Math.abs(zScore) > sensitivity * 2 || percentDeviation > 40) {
        severity = "high";
      } else if (Math.abs(zScore) > sensitivity * 1.5 || percentDeviation > 30) {
        severity = "medium";
      }

      // Generate reason
      let reason = "";
      if (value > expected) {
        reason = `${percentDeviation.toFixed(0)}% above expected (${formatValue(expected)})`;
      } else {
        reason = `${percentDeviation.toFixed(0)}% below expected (${formatValue(expected)})`;
      }

      anomalies.push({
        index,
        value,
        expected,
        deviation,
        severity,
        reason,
      });
    }
  });

  return anomalies;
}

/**
 * Detect seasonal anomalies by comparing to same period in previous cycles
 */
export function detectSeasonalAnomalies(
  data: DataPoint[],
  valueKey: string,
  seasonalPeriod: number = 12 // Default to 12 months
): Anomaly[] {
  if (!data || data.length < seasonalPeriod * 2) {
    return [];
  }

  const anomalies: Anomaly[] = [];
  const values = data.map((d) => Number(d[valueKey] || 0));

  // Compare each point to same period in previous cycle
  for (let i = seasonalPeriod; i < values.length; i++) {
    const current = values[i];
    const historical = values[i - seasonalPeriod];

    if (historical === 0) continue;

    const percentChange = ((current - historical) / historical) * 100;

    // Flag significant changes (>30% or <-30%)
    if (Math.abs(percentChange) > 30) {
      const severity: "low" | "medium" | "high" =
        Math.abs(percentChange) > 50
          ? "high"
          : Math.abs(percentChange) > 40
          ? "medium"
          : "low";

      anomalies.push({
        index: i,
        value: current,
        expected: historical,
        deviation: Math.abs(current - historical),
        severity,
        reason: `${Math.abs(percentChange).toFixed(0)}% ${
          percentChange > 0 ? "increase" : "decrease"
        } vs same period last year`,
      });
    }
  }

  return anomalies;
}

/**
 * Detect sudden spikes or drops in sequential data
 */
export function detectSpikesAndDrops(
  data: DataPoint[],
  valueKey: string,
  threshold: number = 25 // Percentage change threshold
): Anomaly[] {
  if (!data || data.length < 2) {
    return [];
  }

  const anomalies: Anomaly[] = [];
  const values = data.map((d) => Number(d[valueKey] || 0));

  for (let i = 1; i < values.length; i++) {
    const current = values[i];
    const previous = values[i - 1];

    if (previous === 0) continue;

    const percentChange = ((current - previous) / previous) * 100;

    if (Math.abs(percentChange) > threshold) {
      const severity: "low" | "medium" | "high" =
        Math.abs(percentChange) > 50
          ? "high"
          : Math.abs(percentChange) > 35
          ? "medium"
          : "low";

      const isSpike = percentChange > 0;
      anomalies.push({
        index: i,
        value: current,
        expected: previous,
        deviation: Math.abs(current - previous),
        severity,
        reason: `${isSpike ? "Spike" : "Drop"} of ${Math.abs(
          percentChange
        ).toFixed(0)}% from previous period`,
      });
    }
  }

  return anomalies;
}

function formatValue(value: number): string {
  if (value >= 1000000) {
    return `${(value / 1000000).toFixed(1)}M`;
  } else if (value >= 1000) {
    return `${(value / 1000).toFixed(0)}K`;
  }
  return value.toFixed(0);
}
