import { InsightData } from "@/components/InsightsPanel";

interface CacDataPoint {
  channel: string;
  cac: number;
  new_customers: number;
  cac_grade?: string;
}

interface ArpuDataPoint {
  customer_segment: string;
  arpu: number;
  customer_count: number;
}

interface GmvDataPoint {
  month: string;
  gmv: number;
  net_revenue: number;
  discount_rate_pct: number;
  order_count: number;
}

interface AttachRateDataPoint {
  sides_attach_rate_pct: number;
  beverage_attach_rate_pct: number;
  dessert_attach_rate_pct: number;
  total_orders: number;
}

interface ChannelDataPoint {
  channel: string;
  revenue: number;
  orders?: number;
}

export interface DashboardData {
  cacByChannel?: CacDataPoint[];
  arpuBySegment?: ArpuDataPoint[];
  gmvTrend?: GmvDataPoint[];
  attachRate?: AttachRateDataPoint[];
  channelBreakdown?: ChannelDataPoint[];
}

export function generateInsights(data: DashboardData): InsightData[] {
  const insights: InsightData[] = [];

  // Insight 1: CAC Efficiency Analysis
  if (data.cacByChannel && data.cacByChannel.length > 0) {
    const sorted = [...data.cacByChannel].sort((a, b) => Number(a.cac || 0) - Number(b.cac || 0));
    const lowest = sorted[0];
    const highest = sorted[sorted.length - 1];

    const lowestCac = Number(lowest?.cac || 0);
    const highestCac = Number(highest?.cac || 0);

    if (highestCac > lowestCac * 3 && lowestCac > 0) {
      const ratio = (highestCac / lowestCac).toFixed(1);
      insights.push({
        id: "cac-efficiency",
        type: "warning",
        title: "High CAC Disparity Detected",
        message: `Your CAC for ${highest.channel} ($${highestCac.toFixed(2)}) is ${ratio}x higher than ${lowest.channel} ($${lowestCac.toFixed(2)}). Consider reallocating marketing budget to more efficient channels.`,
        metric: {
          value: `${ratio}x`,
          trend: "down",
        },
      });
    }

    // Find best performing channel
    const bestChannel = sorted[0];
    const bestCac = Number(bestChannel?.cac || 0);
    if (bestCac < 10 && bestCac > 0) {
      insights.push({
        id: "cac-success",
        type: "success",
        title: "Excellent CAC Performance",
        message: `${bestChannel.channel} has an outstanding CAC of $${bestCac.toFixed(2)}, well below the QSR industry benchmark of $15-25. Continue investing in this channel.`,
        metric: {
          value: `$${bestCac.toFixed(2)}`,
          trend: "up",
        },
      });
    }
  }

  // Insight 2: ARPU Segment Analysis
  if (data.arpuBySegment && data.arpuBySegment.length > 0) {
    const sorted = [...data.arpuBySegment].sort((a, b) => Number(b.arpu || 0) - Number(a.arpu || 0));
    const highest = sorted[0];
    const average = sorted.reduce((sum, s) => sum + Number(s.arpu || 0), 0) / sorted.length;
    const highestArpu = Number(highest?.arpu || 0);

    if (highestArpu > average * 1.15 && average > 0) {
      insights.push({
        id: "arpu-segment",
        type: "opportunity",
        title: "High-Value Segment Opportunity",
        message: `${highest.customer_segment} customers have ARPU of $${highestArpu.toLocaleString()}, ${(((highestArpu - average) / average) * 100).toFixed(0)}% above average. Focus retention and upsell campaigns on this segment.`,
        metric: {
          value: `$${highestArpu.toLocaleString()}`,
          trend: "up",
        },
      });
    }
  }

  // Insight 3: GMV vs Discount Analysis
  if (data.gmvTrend && data.gmvTrend.length > 0) {
    const recentMonths = data.gmvTrend.slice(-3);
    const avgDiscount = recentMonths.reduce((sum, m) => sum + Number(m.discount_rate_pct || 0), 0) / recentMonths.length;

    if (avgDiscount > 15) {
      insights.push({
        id: "discount-warning",
        type: "alert",
        title: "High Discount Rate",
        message: `Average discount rate over the last 3 months is ${avgDiscount.toFixed(1)}%. High discounting can erode margins. Consider testing targeted offers instead of blanket discounts.`,
        metric: {
          value: `${avgDiscount.toFixed(1)}%`,
          trend: "down",
        },
      });
    }

    // Check for GMV growth
    if (recentMonths.length >= 2) {
      const oldestGmv = Number(recentMonths[0]?.gmv || 0);
      const newestGmv = Number(recentMonths[recentMonths.length - 1]?.gmv || 0);

      if (oldestGmv > 0) {
        const growth = ((newestGmv - oldestGmv) / oldestGmv) * 100;

        if (growth > 10) {
          insights.push({
            id: "gmv-growth",
            type: "success",
            title: "Strong GMV Growth",
            message: `GMV has grown ${growth.toFixed(1)}% over the last ${recentMonths.length} months. Your business is showing healthy growth momentum.`,
            metric: {
              value: `+${growth.toFixed(1)}%`,
              trend: "up",
            },
          });
        } else if (growth < -5) {
          insights.push({
            id: "gmv-decline",
            type: "warning",
            title: "GMV Decline Detected",
            message: `GMV has declined ${Math.abs(growth).toFixed(1)}% over the last ${recentMonths.length} months. Review pricing strategy and marketing campaigns.`,
            metric: {
              value: `${growth.toFixed(1)}%`,
              trend: "down",
            },
          });
        }
      }
    }
  }

  // Insight 4: Attach Rate Opportunities
  if (data.attachRate && data.attachRate.length > 0) {
    const rates = data.attachRate[0];

    // Find highest attach rate
    const rateTypes = [
      { name: "Sides", rate: Number(rates?.sides_attach_rate_pct || 0) },
      { name: "Beverages", rate: Number(rates?.beverage_attach_rate_pct || 0) },
      { name: "Desserts", rate: Number(rates?.dessert_attach_rate_pct || 0) },
    ];
    const sorted = [...rateTypes].sort((a, b) => b.rate - a.rate);
    const highest = sorted[0];
    const lowest = sorted[sorted.length - 1];

    if (highest.rate > lowest.rate * 1.5 && lowest.rate > 0) {
      insights.push({
        id: "attach-rate-opportunity",
        type: "opportunity",
        title: "Upsell Opportunity Identified",
        message: `${highest.name} have a strong ${highest.rate.toFixed(1)}% attach rate, while ${lowest.name} are at ${lowest.rate.toFixed(1)}%. Consider promoting ${lowest.name} with combo deals or suggestive selling.`,
        metric: {
          value: `${highest.rate.toFixed(1)}%`,
          trend: "up",
        },
      });
    }
  }

  // Insight 5: Channel Performance
  if (data.channelBreakdown && data.channelBreakdown.length > 0) {
    const totalRevenue = data.channelBreakdown.reduce((sum, ch) => sum + Number(ch.revenue || 0), 0);
    const sorted = [...data.channelBreakdown].sort((a, b) => Number(b.revenue || 0) - Number(a.revenue || 0));
    const topChannel = sorted[0];
    const topRevenue = Number(topChannel?.revenue || 0);

    if (totalRevenue > 0) {
      const percentage = (topRevenue / totalRevenue) * 100;

      if (percentage > 40) {
        insights.push({
          id: "channel-concentration",
          type: "info",
          title: "Channel Concentration Risk",
          message: `${topChannel.channel} accounts for ${percentage.toFixed(0)}% of revenue. Consider diversifying channel mix to reduce dependency risk.`,
          metric: {
            value: `${percentage.toFixed(0)}%`,
            trend: "down",
          },
        });
      }

      // Identify growing channels
      const digitalChannels = data.channelBreakdown.filter(
        ch => ch.channel === "Mobile App" || ch.channel === "Online"
      );
      if (digitalChannels.length > 0) {
        const digitalRevenue = digitalChannels.reduce((sum, ch) => sum + Number(ch.revenue || 0), 0);
        const digitalPercentage = (digitalRevenue / totalRevenue) * 100;

        if (digitalPercentage > 50) {
          insights.push({
            id: "digital-first",
            type: "success",
            title: "Digital-First Success",
            message: `Digital channels (Mobile App + Online) represent ${digitalPercentage.toFixed(0)}% of revenue. Your digital transformation is driving business growth.`,
            metric: {
              value: `${digitalPercentage.toFixed(0)}%`,
              trend: "up",
            },
          });
        }
      }
    }
  }

  // Limit to top 5 most important insights
  return insights.slice(0, 5);
}
