import { InsightData } from "@/components/InsightsPanel";

interface ExportData {
  metrics?: any;
  revenueTrend?: any[];
  cacByChannel?: any[];
  arpuBySegment?: any[];
  gmvTrend?: any[];
  channelBreakdown?: any[];
  insights?: InsightData[];
  filters?: {
    dateRange: string;
    segment: string;
    channel: string;
    timeGrain: string;
    comparisonMode: string;
  };
}

export function exportToCSV(data: ExportData, filename: string = "dashboard-export") {
  const timestamp = new Date().toISOString().split("T")[0];
  const fullFilename = `${filename}-${timestamp}.csv`;

  // Create CSV content
  let csvContent = "Dashboard Export\n";
  csvContent += `Generated: ${new Date().toLocaleString()}\n\n`;

  // Add filters
  if (data.filters) {
    csvContent += "Active Filters\n";
    csvContent += `Date Range,${data.filters.dateRange}\n`;
    csvContent += `Segment,${data.filters.segment}\n`;
    csvContent += `Channel,${data.filters.channel}\n`;
    csvContent += `Time Grain,${data.filters.timeGrain}\n`;
    csvContent += `Comparison Mode,${data.filters.comparisonMode}\n\n`;
  }

  // Add insights
  if (data.insights && data.insights.length > 0) {
    csvContent += "AI-Generated Insights\n";
    csvContent += "Type,Title,Message\n";
    data.insights.forEach((insight) => {
      const message = insight.message.replace(/,/g, ";");
      csvContent += `${insight.type},"${insight.title || ""}","${message}"\n`;
    });
    csvContent += "\n";
  }

  // Add revenue trend
  if (data.revenueTrend && data.revenueTrend.length > 0) {
    csvContent += "Revenue Trend\n";
    const headers = Object.keys(data.revenueTrend[0]).join(",");
    csvContent += headers + "\n";
    data.revenueTrend.forEach((row) => {
      const values = Object.values(row).join(",");
      csvContent += values + "\n";
    });
    csvContent += "\n";
  }

  // Add CAC by channel
  if (data.cacByChannel && data.cacByChannel.length > 0) {
    csvContent += "Customer Acquisition Cost by Channel\n";
    const headers = Object.keys(data.cacByChannel[0]).join(",");
    csvContent += headers + "\n";
    data.cacByChannel.forEach((row) => {
      const values = Object.values(row).join(",");
      csvContent += values + "\n";
    });
    csvContent += "\n";
  }

  // Add ARPU by segment
  if (data.arpuBySegment && data.arpuBySegment.length > 0) {
    csvContent += "ARPU by Customer Segment\n";
    const headers = Object.keys(data.arpuBySegment[0]).join(",");
    csvContent += headers + "\n";
    data.arpuBySegment.forEach((row) => {
      const values = Object.values(row).join(",");
      csvContent += values + "\n";
    });
    csvContent += "\n";
  }

  // Create and download file
  const blob = new Blob([csvContent], { type: "text/csv;charset=utf-8;" });
  const link = document.createElement("a");
  const url = URL.createObjectURL(blob);
  link.setAttribute("href", url);
  link.setAttribute("download", fullFilename);
  link.style.visibility = "hidden";
  document.body.appendChild(link);
  link.click();
  document.body.removeChild(link);
}

export function exportToJSON(data: ExportData, filename: string = "dashboard-export") {
  const timestamp = new Date().toISOString().split("T")[0];
  const fullFilename = `${filename}-${timestamp}.json`;

  const exportData = {
    metadata: {
      generatedAt: new Date().toISOString(),
      filters: data.filters,
    },
    insights: data.insights,
    metrics: data.metrics,
    revenueTrend: data.revenueTrend,
    cacByChannel: data.cacByChannel,
    arpuBySegment: data.arpuBySegment,
    gmvTrend: data.gmvTrend,
    channelBreakdown: data.channelBreakdown,
  };

  const jsonContent = JSON.stringify(exportData, null, 2);
  const blob = new Blob([jsonContent], { type: "application/json" });
  const link = document.createElement("a");
  const url = URL.createObjectURL(blob);
  link.setAttribute("href", url);
  link.setAttribute("download", fullFilename);
  link.style.visibility = "hidden";
  document.body.appendChild(link);
  link.click();
  document.body.removeChild(link);
}
