export interface ForecastPoint {
  period: string;
  value: number;
  lowerBound: number;
  upperBound: number;
  confidence: number;
}

interface DataPoint {
  [key: string]: any;
}

/**
 * Generate forecast using linear regression with trend analysis
 */
export function generateForecast(
  historicalData: DataPoint[],
  valueKey: string,
  periodKey: string,
  options: {
    periods?: number; // Number of periods to forecast (default: 3)
    confidence?: number; // Confidence level 0-1 (default: 0.95)
  } = {}
): ForecastPoint[] {
  const { periods = 3, confidence = 0.95 } = options;

  if (!historicalData || historicalData.length < 3) {
    return [];
  }

  const values = historicalData.map((d) => Number(d[valueKey] || 0));
  const n = values.length;

  // Calculate linear regression
  const xValues = Array.from({ length: n }, (_, i) => i);
  const xMean = xValues.reduce((sum, x) => sum + x, 0) / n;
  const yMean = values.reduce((sum, y) => sum + y, 0) / n;

  let numerator = 0;
  let denominator = 0;

  for (let i = 0; i < n; i++) {
    numerator += (xValues[i] - xMean) * (values[i] - yMean);
    denominator += Math.pow(xValues[i] - xMean, 2);
  }

  const slope = denominator !== 0 ? numerator / denominator : 0;
  const intercept = yMean - slope * xMean;

  // Calculate residual standard error for confidence intervals
  const residuals = values.map((y, i) => y - (slope * i + intercept));
  const rss = residuals.reduce((sum, r) => sum + r * r, 0);
  const standardError = Math.sqrt(rss / (n - 2));

  // Z-score for confidence level (95% = 1.96, 90% = 1.645)
  const zScore = confidence === 0.95 ? 1.96 : confidence === 0.90 ? 1.645 : 2.0;

  // Generate forecast points
  const forecast: ForecastPoint[] = [];
  const lastPeriod = historicalData[n - 1][periodKey];

  for (let i = 1; i <= periods; i++) {
    const x = n - 1 + i;
    const predictedValue = slope * x + intercept;

    // Widen confidence interval for further predictions
    const distanceFactor = 1 + (i - 1) * 0.2; // Increase uncertainty by 20% per period
    const margin = zScore * standardError * distanceFactor;

    // Generate period label (assuming monthly data)
    const period = generateNextPeriod(lastPeriod, i);

    forecast.push({
      period,
      value: Math.max(0, predictedValue), // Ensure non-negative
      lowerBound: Math.max(0, predictedValue - margin),
      upperBound: predictedValue + margin,
      confidence: confidence * 100,
    });
  }

  return forecast;
}

/**
 * Generate forecast using exponential smoothing (better for data with trends)
 */
export function generateExponentialForecast(
  historicalData: DataPoint[],
  valueKey: string,
  periodKey: string,
  options: {
    periods?: number;
    alpha?: number; // Smoothing factor 0-1 (default: 0.3)
    beta?: number; // Trend smoothing 0-1 (default: 0.1)
  } = {}
): ForecastPoint[] {
  const { periods = 3, alpha = 0.3, beta = 0.1 } = options;

  if (!historicalData || historicalData.length < 3) {
    return [];
  }

  const values = historicalData.map((d) => Number(d[valueKey] || 0));
  const n = values.length;

  // Initialize level and trend
  let level = values[0];
  let trend = values[1] - values[0];

  // Holt's linear exponential smoothing
  const smoothed: number[] = [level];

  for (let i = 1; i < n; i++) {
    const prevLevel = level;
    level = alpha * values[i] + (1 - alpha) * (prevLevel + trend);
    trend = beta * (level - prevLevel) + (1 - beta) * trend;
    smoothed.push(level);
  }

  // Calculate standard error for confidence intervals
  const errors = values.map((v, i) => Math.abs(v - smoothed[i]));
  const mae = errors.reduce((sum, e) => sum + e, 0) / n;

  // Generate forecasts
  const forecast: ForecastPoint[] = [];
  const lastPeriod = historicalData[n - 1][periodKey];

  for (let i = 1; i <= periods; i++) {
    const predictedValue = level + i * trend;
    const margin = mae * (1 + i * 0.3); // Increase uncertainty over time

    const period = generateNextPeriod(lastPeriod, i);

    forecast.push({
      period,
      value: Math.max(0, predictedValue),
      lowerBound: Math.max(0, predictedValue - margin),
      upperBound: predictedValue + margin,
      confidence: 85, // Lower confidence for exponential smoothing
    });
  }

  return forecast;
}

/**
 * Generate seasonal forecast considering historical patterns
 */
export function generateSeasonalForecast(
  historicalData: DataPoint[],
  valueKey: string,
  periodKey: string,
  seasonalPeriod: number = 12
): ForecastPoint[] {
  if (!historicalData || historicalData.length < seasonalPeriod * 2) {
    return [];
  }

  const values = historicalData.map((d) => Number(d[valueKey] || 0));
  const n = values.length;

  // Calculate seasonal indices
  const seasonalIndices: number[] = [];
  for (let i = 0; i < seasonalPeriod; i++) {
    const seasonValues: number[] = [];
    for (let j = i; j < n; j += seasonalPeriod) {
      seasonValues.push(values[j]);
    }
    const avgSeason =
      seasonValues.reduce((sum, v) => sum + v, 0) / seasonValues.length;
    seasonalIndices.push(avgSeason);
  }

  const overallMean = values.reduce((sum, v) => sum + v, 0) / n;

  // Calculate trend from deseasonalized data
  const deseasonalized = values.map((v, i) => {
    const seasonIndex = i % seasonalPeriod;
    return seasonalIndices[seasonIndex] > 0
      ? v / (seasonalIndices[seasonIndex] / overallMean)
      : v;
  });

  // Simple linear trend on deseasonalized data
  const xValues = Array.from({ length: n }, (_, i) => i);
  const xMean = xValues.reduce((sum, x) => sum + x, 0) / n;
  const yMean = deseasonalized.reduce((sum, y) => sum + y, 0) / n;

  let numerator = 0;
  let denominator = 0;
  for (let i = 0; i < n; i++) {
    numerator += (xValues[i] - xMean) * (deseasonalized[i] - yMean);
    denominator += Math.pow(xValues[i] - xMean, 2);
  }

  const slope = denominator !== 0 ? numerator / denominator : 0;
  const intercept = yMean - slope * xMean;

  // Generate seasonal forecasts
  const forecast: ForecastPoint[] = [];
  const lastPeriod = historicalData[n - 1][periodKey];

  for (let i = 1; i <= 3; i++) {
    const x = n - 1 + i;
    const trendValue = slope * x + intercept;
    const seasonIndex = (n - 1 + i) % seasonalPeriod;
    const seasonalFactor = seasonalIndices[seasonIndex] / overallMean;
    const predictedValue = trendValue * seasonalFactor;

    const period = generateNextPeriod(lastPeriod, i);

    forecast.push({
      period,
      value: Math.max(0, predictedValue),
      lowerBound: Math.max(0, predictedValue * 0.85),
      upperBound: predictedValue * 1.15,
      confidence: 80,
    });
  }

  return forecast;
}

/**
 * Generate next period label (assumes YYYY-MM format)
 */
function generateNextPeriod(lastPeriod: string, offset: number): string {
  // Handle YYYY-MM format
  if (lastPeriod.match(/^\d{4}-\d{2}$/)) {
    const [year, month] = lastPeriod.split("-").map(Number);
    const date = new Date(year, month - 1 + offset, 1);
    const newYear = date.getFullYear();
    const newMonth = String(date.getMonth() + 1).padStart(2, "0");
    return `${newYear}-${newMonth}`;
  }

  // Handle YYYY-MM-DD format
  if (lastPeriod.match(/^\d{4}-\d{2}-\d{2}$/)) {
    const date = new Date(lastPeriod);
    date.setMonth(date.getMonth() + offset);
    return date.toISOString().split("T")[0];
  }

  // Fallback
  return `Forecast +${offset}`;
}
